body {
    margin: 0;
    ul {
        margin: 0;
        padding: 0;
        li {
            list-style: none;
        }
    }
    h1, h2, h3, h4, h5, h6  {
        margin: 0;
    }
    a {
        text-decoration: none;
    }
    p {
        margin: 0;
    }
    img {
        max-width: 100%;
    }
    .header {
        padding: 16px;
    }
    .main {
        padding: 16px;
    }
}

@media(min-width: 720px) {
    body {
        .main {
            padding-left: 0;
            padding-right: 0;
        }
    }
}